form {
  width: 50%;
  margin: 15vh 35%;
  float: center; }

.search {
  padding: 10px 15px;
  background: rgba(50, 50, 50, 0.2);
  border: 2px solid #dbdbdb;
  font-size: 20px; }

.button {
  position: relative;
  padding: 10px 15px;
  left: -1px;
  border: 2px solid #207cca;
  background-color: #207cca;
  color: #fafafa;
  font-size: 20px; }

.button:hover {
  background-color: #fafafa;
  color: #207cca; }



